import React, { Component } from 'react';
import PointTable from './PointTable';

const _ = require('lodash');
const rws = require('robust-websocket');

class CardTable extends Component {
  
  constructor(props) {
    super(props);
    
    this.state = {
      points: [],
      timer: 0,
      playerInfo: null,

      testPoints: [], // test points
    }
    
    this.timer = 999
    this.bankerPoint = 0
    this.playerPoint = 0
    this.gameNo = null

    this.runTest = this.runTest.bind(this);
    this.loopCall = this.loopCall.bind(this);
  }
  
  runTest() {
    _.delay(this.loopCall, 5000)
  }
  
  loopCall() {
    let array = this.randomPoints()
    this.setState({
      testPoints: _.concat(this.state.testPoints, array)
    })
    this.runTest()
  }
  
  randomPoints() {
    const number = _.random(0, 2)
    const tables = ["A", "B", "C"]
    let point = {
      table: tables[number],
      type: number === 1 ? 'Banker' : number === 2 ? 'Player' : 'Tie',
      value: number,
      time: Date.now()
    }
    return [point]
  }  
  
  initSocket() {
    const table = _.get(this.props, 'table', 'x')
    const url = `wss://mobilegs${_.toLower(table)}.bacc6666.com/`
    const ws = new rws(url, null, {
        shouldReconnect: (event, wss) => {
          return Math.pow(1.5, ws.attempts) * 500
        }
      });

    ws.addEventListener('open', this.openedSocket);

    ws.addEventListener('message', this.receivedMessage);

    ws.addEventListener('close', this.closedSocket);
  }

  openedSocket = (event) => {
    //console.log('__OPEN__')
  }

  receivedMessage = (event) => {
    /*
    // array
    [
      "0",                    -> 0
      "010119014",            -> 1
      "0738",                 -> 2
      "13",                   -> 3 // P turn
      "42",                   -> 4 // B turn
      "37",                   -> 5 // P turn
      "3A",                   -> 6 // B turn
      "21",                   -> 7 // P turn 
      "23",                   -> 8 // B turn
      "5",                    -> 9  // Banker Point
      "1",                    -> 10 // Player Point
      "1",                    -> 11 
      "3",                    -> 12 
      "2018-01-20 01:29:50",  -> 13
      "0",                    -> 14 // Timer count down from 25 to 0
      "2",                    -> 15 // Game state if value > 0 then should add points to stack
      "0",                    -> 16
      "0",                    -> 17
      "998",                  -> 18
      "↵"                     -> 19
    ]
  */
    try {
      const table = _.get(this.props, 'table', '-')
      const string = event.data
      const hasPointsData = _.startsWith(string, '0,', 0)
      //console.log(_.split(string, ','))
      //console.log(this.gameNo, table)
      if (hasPointsData) {
        const dataArray = _.split(string, ',')
        const groupData = _.chain(dataArray).filter((value, index) => {
          return _.includes([1, 9, 10, 14], index)
        }).map((value) => { return _.toSafeInteger(value) }).value()

        const gameNo = groupData[0]
        const bankerPoint = groupData[1]
        const playerPoint = groupData[2]
        const timer = groupData[3]
        const isCardUp = timer === 0 ? true : false

        // Reset points when start new game
        if (this.gameNo && this.gameNo !== gameNo) {
          this.setState({
            points: []
          })
        }

        this.gameNo = gameNo
        
        // Show card
        if (isCardUp) {
          this.bankerPoint = bankerPoint
          this.playerPoint = playerPoint
          console.log('Table:', table,' Banker: ', bankerPoint, ' Player: ', playerPoint)
          console.log('---')
        }

        // Update points
        const newGameWillStart = timer > this.timer
        if (newGameWillStart) {
          const type = this.bankerPoint === this.playerPoint ? 'Tie' : this.bankerPoint > this.playerPoint ? 'Banker' : 'Player'
          const value = type === 'Tie' ? this.bankerPoint || this.playerPoint : type === 'Banker' ? this.bankerPoint : this.playerPoint
          const point = {
            table: table,
            type: type,
            value: value,
            time: Date.now()
          }
          const css = "background-color: #f0a; color: #fff; padding: 3px 10px; font-weight: bold;"
          console.log(`%cTable: ${table} | Winner: ${type} | Point: ${value}`, css)
          console.log('---')
          const points = _.compact(_.concat(this.state.points, [point]))
          this.setState({
            points: points
          })
        }

        // Timer
        this.timer = timer
        this.setState({
          timer : this.timer
        })
      }

      // Update summary data
      const summaryData = _.startsWith(string, '$RC,', 0)
      if (summaryData) {
        const array = _.split(string, ',')
        const lastValue = _.last(array)
        const transformValue = _.split(lastValue, '$').join(',')
        const json = JSON.parse(transformValue)
        //console.log(json.PlayerInfo)
        this.setState({
          playerInfo: json.PlayerInfo
        })

        this.gameNo = _.toSafeInteger(json.PlayerInfo.NoRun)
      }

    } catch (err) {
      console.log(err)
    }
  }

  closedSocket = (event) => {
    //console.log('__close__')
  }

  getTimer() {
    return (this.state.timer > 0) ? this.state.timer : "🕒" //"🃏"
  }

  checkStyle(checkCase) {
    return { color: '#212529' }
  }

  renderTable() {
    let info = this.state.playerInfo

    return (
      <table className="table table-sm table-striped">
        <tbody>

          <tr>
            <th scope="row" colSpan="2">
              {_.get(this.props, 'table', 'Untitled')}
            </th>
          </tr>

          <tr>
            <th scope="row">
              Timer
        </th>
            <td>
              <span className="text-info">{this.getTimer()}</span>
            </td>
          </tr>

          <tr>
            <th scope="row">Play</th>
            <td style={this.checkStyle('Total')}>
              {_.get(info, 'Total', 0)}
            </td>

          </tr>

          <tr>

            <th scope="row">
              <span className="badge badge-pill badge-danger">Banker</span>
            </th>
            <td style={this.checkStyle('Banker')}>
              {_.get(info, 'Banker', 0)}
            </td>

          </tr>

          <tr>
            <th scope="row">
              <span className="badge badge-pill badge-primary">Player</span>
            </th>
            <td style={this.checkStyle('Player')}>
              {_.get(info, 'Player', 0)}
            </td>
          </tr>
          <tr>
            <th scope="row">
              <span className="badge badge-pill badge-success">Tie</span>
            </th>
            <td style={this.checkStyle('Tie')}>
              {_.get(info, 'Tie', 0)}
            </td>

          </tr>

        </tbody>
      </table>
    )
  }

  componentDidMount() {
    // this.runTest()
    this.initSocket()
  }
  
  render() {    
    return (
      <div className="card">
      
      <div className="card-body">
      
      {/* New Layout */}
      <div className="row no-gutters">
      <div className="col-sm-2 border-sm-bottom">
      {
        this.renderTable()
      }
      </div>
      <div className="col-sm-10">
      <PointTable column={this.props.column} points={this.state.points} onBreak={this.state.onBreak} />
      {/* <PointTable points={this.state.testPoints} /> */}
      </div>
      </div>
      
      </div>
      
      </div>
    )
  }
  
}

export default CardTable


