import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import CardTable from './components/CardTable';

const _ = require('lodash');
const rws = require('robust-websocket');

class App extends Component {
  
  constructor(props) {
    super(props);
    
    this.state = {
      /// suffix meaning: B = Bacc, I = InsuBacc
      AB: null,
      AI: null,
      
      BB: null,
      BI: null,
      
      CB: null,
      CI: null,
      
      DB: null,
      DI: null,
      
      EB: null,
      EI: null,
      
      FB: null,
      FI: null,
      
      HB: null,
      HI: null,
      
      IB: null,
      II: null,
      
      JB: null,
      JI: null,
      
      // Column
      maxHistoryCount: 20,
      gameType: 'Bacc',
    }
    
    this.values = []
    
    this.switchGameType = this.switchGameType.bind(this)
  }
  
  initSocket() {
    const ws = 
    new rws('wss://mobileroad.bacc6666.com/', null, {
    shouldReconnect: (event, wss) => {
      return Math.pow(1.5, ws.attempts) * 500
    }
  });
  
  ws.addEventListener('open', this.openedSocket);
  
  ws.addEventListener('message', this.receivedMessage);
  
  ws.addEventListener('close', this.closedSocket);
}

openedSocket = (event) => {
  //console.log('__OPEN__')
}

receivedMessage = (event) => {
  //console.log(event.data)
  let parseJson = JSON.parse(event.data)
  if (parseJson.m_now.tp === 'Bacc' || parseJson.m_now.tp === 'InsuBacc') {
    
    let history = parseJson.m_RoadHisCnt
    let mNow = parseJson.m_now
    
    switch (mNow.did) {
      case 'A':
      if(mNow.tp === 'Bacc') {
        this.setState({ AB: parseJson })
      }
      if(mNow.tp === 'InsuBacc') {
        this.setState({ AI: parseJson })
      }
      break
      
      case 'B':
      if (mNow.tp === 'Bacc') {
        this.setState({ BB: parseJson })
      }
      if (mNow.tp === 'InsuBacc') {
        this.setState({ BI: parseJson })
      }
      break
      
      case 'C':
      if (mNow.tp === 'Bacc') {
        this.setState({ CB: parseJson })
      }
      if (mNow.tp === 'InsuBacc') {
        this.setState({ CI: parseJson })
      }
      break
      
      case 'D':
      if (mNow.tp === 'Bacc') {
        this.setState({ DB: parseJson })
      }
      if (mNow.tp === 'InsuBacc') {
        this.setState({ DI: parseJson })
      }
      break
      
      case 'E':
      if (mNow.tp === 'Bacc') {
        this.setState({ EB: parseJson })
      }
      if (mNow.tp === 'InsuBacc') {
        this.setState({ EI: parseJson })
      }
      break
      
      case 'F':
      if (mNow.tp === 'Bacc') {
        this.setState({ FB: parseJson })
      }
      if (mNow.tp === 'InsuBacc') {
        this.setState({ FI: parseJson })
      }
      break
      
      case 'H':
      if (mNow.tp === 'Bacc') {
        this.setState({ HB: parseJson })
      }
      if (mNow.tp === 'InsuBacc') {
        this.setState({ HI: parseJson })
      }
      break
      
      case 'I':
      if (mNow.tp === 'Bacc') {
        this.setState({ IB: parseJson })
      }
      if (mNow.tp === 'InsuBacc') {
        this.setState({ II: parseJson })
      }
      break
      
      case 'J':
      if (mNow.tp === 'Bacc') {
        this.setState({ JB: parseJson })
      }
      if (mNow.tp === 'InsuBacc') {
        this.setState({ JI: parseJson })
      }
      break
      
      default: break;
    }
    
    //this.values.push(_.get(history, 'HistoryCnt', 0))
    
    //console.log(parseJson)
    //console.log(mNow)
    console.log('Table: ', mNow.did, '\nTm: ', mNow.tm,'\nSt: ', mNow.st, '\nWin:', _.get(mNow, 'win', '-'))
    console.log('Banker: ', history.Banker, '\nPlayer: ', history.Player, '\nDBanker: ', history.DBanker, '\nDPlayer: ', history.DPlayer, '\nTie: ', history.Tie, '\nHistoryCount: ', history.HistoryCnt)
    console.log('***************************************')
  }
  
}

closedSocket = (event) => {
  // console.log('__CLOSE__')  
  // let max = _.max(this.values)
  
  // if (max && max > this.state.maxHistoryCount) {
  //   this.setState({
  //     maxHistoryCount: max
  //   })
  // }
  
  // this.values = []
}

switchGameType(e, gameType) {
  e.preventDefault()
  
  this.setState({
    gameType: gameType,
    maxHistoryCount: 20,
  })
  
}

checkStatus(mode) {
  return (mode === this.state.gameType) ? " active" : ""
}

getData(table) {
  switch (table) {
    case 'A':
    return this.state.gameType === 'Bacc' ? this.state.AB : this.state.AI   
    
    case 'B':
    return this.state.gameType === 'Bacc' ? this.state.BB : this.state.BI
    
    case 'C':
    return this.state.gameType === 'Bacc' ? this.state.CB : this.state.CI
    
    case 'D':
    return this.state.gameType === 'Bacc' ? this.state.DB : this.state.DI
    
    case 'E':
    return this.state.gameType === 'Bacc' ? this.state.EB : this.state.EI
    
    case 'F':
    return this.state.gameType === 'Bacc' ? this.state.FB : this.state.FI
    
    case 'H':
    return this.state.gameType === 'Bacc' ? this.state.HB : this.state.HI
    
    case 'I':
    return this.state.gameType === 'Bacc' ? this.state.IB : this.state.II
    
    case 'J':
    return this.state.gameType === 'Bacc' ? this.state.JB : this.state.JI
    
    default: break;
  }
}

componentDidMount() {
  //this.initSocket()
}

render() {
  return (
    
    <div data-reactroot>
    <div className="App">
    
    <div className= "container-fluid">
    
    
    <header className="App-header">
    <img src={logo} className="App-logo" alt="logo" />
    <h1 className="App-title">The Gray Area</h1>
    </header>
    
{/*     
    <nav aria-label="Page navigation">
    <ul className="pagination justify-content-center">
    
    <li className={"page-item " + this.checkStatus('Bacc')}><a className="page-link" href="#bacc" onClick={(e) => this.switchGameType(e, 'Bacc')}>Bacc</a></li>
    <li className={"page-item " + this.checkStatus('InsuBacc')}><a className="page-link" href="#insubacc" onClick={(e) => this.switchGameType(e, 'InsuBacc')}>InsuBacc</a></li>
    
    </ul>
    </nav> */}
    
    {/* A */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('A')} mode={this.state.gameType} table={'A'} column={this.state.maxHistoryCount} />
    </div>
    
    {/* B */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('B')} mode={this.state.gameType} table={'B'} column={this.state.maxHistoryCount}/>
    </div>
    
    {/* C */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('C')} mode={this.state.gameType} table={'C'} column={this.state.maxHistoryCount}/>
    
    </div>
    
    {/* D E F */}
    {/* D */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('D')} mode={this.state.gameType} table={'D'} column={this.state.maxHistoryCount} />
    </div>

    {/* E */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('E')} mode={this.state.gameType} table={'E'} column={this.state.maxHistoryCount} />
    </div>

    {/* F */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('F')} mode={this.state.gameType} table={'F'} column={this.state.maxHistoryCount} />
    </div>
    
    {/* H */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('H')} mode={this.state.gameType} table={'H'} column={this.state.maxHistoryCount} />
    </div>
    {/* I */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('I')} mode={this.state.gameType} table={'I'} column={this.state.maxHistoryCount} />
    </div>
    {/* J */}
    <div className="card-deck mr-3 ml-3 mb-3">
    <CardTable data={this.getData('J')} mode={this.state.gameType} table={'J'} column={this.state.maxHistoryCount} />
    </div>
    
    </div>
    
    <footer className="blog-footer">
    <p>&copy; tga 2017</p>
    </footer>
    
    </div>
    
    </div>
  );
}
}

export default App;



