import React, { Component } from 'react';
const _ = require('lodash');

class PointTable extends Component {
  
  constructor(props) {
    super(props);
    
    this.state = {
      row: _.toArray(_.repeat('r', 6)),
    }
    
    this.recentCol = null
    this.recentTable = null
  }
  
  genColumnArrayByNumber(number) {
    return _.toArray(_.repeat('c', number))
  }
  
  renderUngroupColumn() {
    return (<div className="col-sm-4 borderHere">
    
    <table className="table table-sm table-responsive table-bordered">
    <tbody>
    {this.renderUnGroupPoints()}
    </tbody>
    </table>
    
    </div>)
  }
  
  renderTable() {
    return (
      <div className="row no-gutters">
      
      {
        //this.renderUngroupColumn()
      }
      
      <div className="col">
      
      <table ref={node => this.recentTable = node} className="table table-sm table-responsive table-bordered">
      <tbody>
      {this.renderGroupPoints()}
      </tbody>
      </table>
      
      </div>
      
      </div>
    )
  }
  
  groupPoints() {
    const pointsOrderByTime = _.orderBy(this.props.points || [], (point) => {
      return point && point.time;
    }, 'asc');
    
    let groupPoints = [];
    let latestTopStackPoint = null;
    
    _.forEach(pointsOrderByTime, (point, index) => {
      
      if(groupPoints.length > 0) {
        latestTopStackPoint = _.find(_.last(groupPoints), (object) => {
          return object.type !== 'Tie'
        })
      }
      
      if ((latestTopStackPoint && latestTopStackPoint.type !== point.type) && (point.type !== 'Tie')) {
        groupPoints.push([point])
      } else {
        if (groupPoints.length > 0) {
          let latestRows = _.last(groupPoints)
          latestRows.push(point);
          if(latestRows.length > 6) {
            const splice = latestRows.splice(6)
            groupPoints.push(splice)
          }
        } else {
          groupPoints.push([point])
        }
      }
    });
    
    return groupPoints
  }
  
  renderPoint(point, col) {
    if (point) {
      return (
        <td ref={node => this.recentCol = node } key={col}>
        {
          (point.type === 'Player') ? (
            <span className="badge badge-pill badge-primary">{point.value}</span>
          ) :
          (point.type === 'Banker') ? (
            <span className="badge badge-pill badge-danger">{point.value}</span>
          ) : (
            <span className="badge badge-pill badge-success">{point.value}</span>
          )
        }
        </td>
      )
    } else {
      return this.renderEmptyCol(col)
    }
  }
  
  renderEmptyCol(col) {
    return (
      <td key={col}>
      <span className="badge badge-pill">{'\t'}</span>
      </td>
    )
  }
  
  renderGroupPoints() {
    const groupPoints = this.groupPoints();
    const column = this.genColumnArrayByNumber(groupPoints.length || 1)
    
    return _.map(this.state.row, (val, row) => {
      return (
        <tr key={row}>
        {
          _.map(column, (val, col) => {
            const pointColumn = groupPoints[col]
            
            if (pointColumn) {
              const point = pointColumn[row]
              
              return this.renderPoint(point, col)
            } else {
              return this.renderEmptyCol(col)
            }
          })
        }
        </tr>
      )
    })
    
  }
  
  renderUnGroupPoints() {
    const groupPointsByTime = _.orderBy(this.props.points || [], (val) => {
      return val && val.time
    }, 'asc')
    
    const pointsChunk = _.chunk(groupPointsByTime, 6)
    const column = this.genColumnArrayByNumber(9 + pointsChunk.length)
    
    return _.map(this.state.row, (val, row) => {
      return (
        <tr key={row}>
        {
          _.map(column, (val, col) => {
            const pointColumn = pointsChunk[col]
            
            if (pointColumn) {
              const point = pointColumn[row]
              
              return this.renderPoint(point, col)
            } else {
              return this.renderEmptyCol(col)
            }            
          })
        }
        </tr>
      )
    })
  }
  
  renderOnBreak() {
    return (
      <div className="d-flex flex-row align-items-center justify-content-center" style={{height: 199}}>
      <div className="p-2">On a Break</div>
      </div>
    )
  }

  moveToRecentUpdateColumn() {
    if (this.recentCol && this.recentTable) {
      const parentPos = this.recentTable.getBoundingClientRect()
      const childPos = this.recentCol.getBoundingClientRect()
      this.recentTable.scrollLeft = childPos.left - parentPos.left
      console.log(childPos.left - parentPos.left)
    }
  }
  
  shouldComponentUpdate(nextProps) {
    const shouldUpdate = this.props.points.length !== nextProps.points.length
    return shouldUpdate
  }
  
  render() {
    return (
      <div>
      
      {
        this.props.onBreak ? this.renderOnBreak() : this.renderTable()
      }
      
      </div>
    )
  }
  
}

export default PointTable


